.wrapper{color:#363636;font-weight:700;margin:36px 50px 0 50px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding-bottom:77px}@media (max-width: 425px){.wrapper{margin-left:1%;margin-right:1%}}.wrapper__title{color:#45852D;text-align:center;margin-top:0}.wrapper__filter{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}@media (max-width: 425px){.wrapper__filter{-webkit-justify-content:center;justify-content:center;width:100%}}.wrapper__filter-label{margin-right:0.7em}.wrapper__filter-input{border:1px solid #D8D8D8;border-radius:25px;padding:15px 20px;width:30%;font-size:16px}@media (max-width: 700px){.wrapper__filter-input{width:45%}}@media (max-width: 425px){.wrapper__filter-input{width:60%}}.wrapper__content{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:100%;margin-top:5px;-webkit-justify-content:space-between;justify-content:space-between;min-height:350px}.wrapper .card{-webkit-transform:scale(1);transform:scale(1);transition:all 0.5s ease;width:30%;background-color:#F8F8F8;margin:38px 1em 0 1em;border:1px solid #D8D8D8;color:#363636;text-decoration:none;-webkit-flex-grow:1;flex-grow:1;max-width:50%}@media (max-width: 700px){.wrapper .card{width:80%;max-width:none;max-width:initial}}@media (max-width: 425px){.wrapper .card{width:100%;max-width:none;max-width:initial;margin-top:20px}}.wrapper .card:hover{-webkit-transform:scale(1.01);transform:scale(1.01)}.wrapper .card__img-wrapper{position:relative;display:-webkit-flex;display:flex}.wrapper .card__img-wrapper img{width:100%}.wrapper .card__img-wrapper__corner{position:absolute;color:#fff;bottom:0;right:0;font-size:12px;padding:8px 11px 5px 10px}.wrapper .card__img-wrapper__corner p{margin:0}.wrapper .card__img-wrapper__corner--indep{background:#006F79}.wrapper .card__img-wrapper__corner--support{background:#EC6608}.wrapper .card__img-wrapper__corner--none{display:none}.wrapper .card__info{margin-left:20px;font-weight:400}.wrapper .card__info__title{font-size:20px;margin:15px 0 5px 0;font-weight:700}.wrapper .card__info__address{font-size:16px;margin-top:0}.wrapper .card__info__sale{margin-bottom:7px}.wrapper .card__info__shared{margin-top:0;font-size:14px}.wrapper__btn{transition:all 0.3s ease;display:-webkit-flex;display:flex;-webkit-align-self:center;align-self:center;margin-top:60px;border-radius:25px;border:1px solid #D8D8D8;width:-webkit-max-content;width:max-content;padding:12px 33px}.wrapper__btn:hover{cursor:pointer;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.2)}.wrapper__btn span{margin-right:10px}

